/**
 * Setting border-box is required
 */
html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

.row {
  display: -webkit-flex;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start; }

.row {
  flex-direction: row; }
  .row.reverse {
    flex-direction: row-reverse; }
  .row.center {
    justify-content: center; }
  .row.end {
    justify-content: flex-end; }
  .row.middle {
    align-items: center; }
  .row.bottom {
    align-items: flex-end; }

.row.fill, .column.fill {
  align-items: stretch; }

.around {
  justify-content: space-around; }

.between {
  justify-content: space-between; }

[class*="c-"], [class*="tn-"], [class*="xs-"], [class*="sm-"],
[class*="md-"], [class*="lg-"], [class*="xl-"] {
  flex-basis: 100%; }
  [class*="c-"].fill, [class*="tn-"].fill, [class*="xs-"].fill, [class*="sm-"].fill,
  [class*="md-"].fill, [class*="lg-"].fill, [class*="xl-"].fill {
    align-self: stretch; }
  [class*="c-"].no-fill, [class*="tn-"].no-fill, [class*="xs-"].no-fill, [class*="sm-"].no-fill,
  [class*="md-"].no-fill, [class*="lg-"].no-fill, [class*="xl-"].no-fill {
    align-self: flex-start; }

.c-auto, .tn-auto, .xs-auto, .sm-auto,
.md-auto, .lg-auto, .xl-auto {
  flex-basis: 0; }

.start {
  align-self: flex-start; }

.row > .middle {
  align-self: center; }

.row > .bottom {
  align-self: flex-end; }

